const renderBlock = block => {
  if (block.type === 'p') {
    return `<p>${block.text}</p>`;
  }

  if (block.type === 'ol' || block.type === 'ul') {
    const items = block.items ?? [];
    const itemsHtml = items.map(text => `<li>${text}</li>`).join('');
    return `<${block.type}>${itemsHtml}</${block.type}>`;
  }

  return '';
};

const renderFaqItem = ({ q, blocks = [] }) => `
  <li class="ac">
    <h3 class="ac-header">
      <button class="ac-trigger" type="button" aria-label="–ü–∏—Ç–∞–Ω–Ω—è">
        <span class="faq-question">${q}</span>
        <span class="faq-icon">
          <svg aria-hidden="true" width="24" height="24">
            <use href="${BASE}sprite.svg#icon-add"></use>
          </svg>
        </span>
      </button>
    </h3>

    <div class="ac-panel" role="region" aria-labelledby="–ü–∏—Ç–∞–Ω–Ω—è">
      <div class="faq-content">
        ${blocks.map(renderBlock).join('')}
      </div>
    </div>
  </li>
`;

refs.faqListEl.innerHTML = faqData.map(renderFaqItem).join('');

new Accordion('.accordion-container', {
  duration: 1000,
  showMultiple: false,
  collapse: true,
  ariaEnabled: true,
});


const faqAnim = document.querySelector('.faq-ajax-loader');
const total = 20;

for (let i = 0; i < total; i++) {
  const paw = document.createElement('div');
  paw.classList.add('paw');

  paw.style.animationDelay = `${(total - i) * 0.25 + 5}s`;

  const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
  svg.classList.add('icon');

  const use = document.createElementNS('http://www.w3.org/2000/svg', 'use');
  use.setAttributeNS('http://www.w3.org/1999/xlink', 'xlink:href', '#paw1');

  svg.appendChild(use);
  paw.appendChild(svg);
  faqAnim.appendChild(paw);
}
